<template>
    <node :title="config.name" :show-error="showError" :content="content" :error-info="errorInfo"
        @selected="$emit('selected')" @delNode="$emit('delNode')" @insertNode="type => $emit('insertNode', type)"
        placeholder="请设置子流程" header-bgc="#9400d3" header-icon="el-icon-guide" />
</template>

<script>
import Node from './Node'

export default {
    name: "ChildProcessNode",
    props: {
        config: {
            type: Object,
            default: () => {
                return {}
            }
        }
    },
    mounted() {
        console.log(this.setup)
    },
    watch: {
        content(v) {

            this.config.placeholder = v;
        }
    },
    components: { Node },
    data() {
        return {
            showError: false,
            errorInfo: '',
        }
    },
    computed: {
        setup() {
            return this.$store.state.design
        },
        content() {

            var childProcessName = this.config.props.childProcessName;


            return childProcessName;
        }
    },
    methods: {
        //校验数据配置的合法性
        validate(err) {

            console.log("验证子流程数据:", this.config)
            var props = this.config.props;
            if (props.startUserType === 2) {
                //选择表单
                if (!this.$isNotBlank(props.startUserForm)) {
                    err.push("请选择发起人表单")
                    return false;
                }

            }
            //选择子流程
            if (!this.$isNotBlank(props.childProcessId)) {
                err.push("请选择子流程")
                return false;
            }
            //主子-表单映射
            var inputFormMapping = props.inputFormMapping;
             

            for (var item of inputFormMapping) {
                
                if (!this.$isNotBlank(item.mainId) || !this.$isNotBlank(item.childId)) {
                    err.push("请选择主子流程表单映射")
                    return false;
                }
            }
            
            //子主-表单映射
            var outputFormMapping = props.outputFormMapping;
             

            for (var item of outputFormMapping) {
                
                if (!this.$isNotBlank(item.mainId) || !this.$isNotBlank(item.childId)) {
                    err.push("请选择子主流程表单映射")
                    return false;
                }
            }
            return true
        }
    }
}
</script>

<style scoped></style>
