<template>
    <div>
        <el-form-item label="提示文字">
            <el-input size="small" v-model="value.placeholder" placeholder="请设置提示语" />
        </el-form-item>
        <el-form label-position="top">
            <el-form-item label="选项设置" class="options">
                <div slot="label" class="option-item-label">
                    <span>选项设置</span>


                    <!--          <el-button icon="el-icon-plus" type="text" size="mini"-->
                    <!--                     @click="value.options.push({-->
                    <!--                     key:'',value:''-->
                    <!--                     })">新增选项</el-button>-->
                </div>

                <template v-if="!value.optionsFromRemote">
                    <el-button icon="el-icon-plus" type="text" size="mini" @click="value.options.push({
                        key: '', value: ''
                    })">新增选项
                    </el-button>
                    <draggable :list="value.options" group="option" handler=".el-icon-rank" :options="dragOption">
                        <div v-for="(op, index) in value.options" :key="index" class="option-item">
                            <i class="el-icon-rank"></i>

                            <el-input v-model="value.options[index].key" clearable placeholder="请设置选项key值"></el-input>
                            <el-input v-model="value.options[index].value" size="medium" placeholder="请设置选项value值"
                                clearable>

                                <!--                <el-input style="width: 20%" size="medium" slot="prepend" placeholder="请选择"></el-input>-->

                                <el-button icon="el-icon-delete" slot="append" type="danger" size="medium"
                                    @click="value.options.splice(index, 1)"></el-button>
                            </el-input>
                        </div>
                    </draggable>
                </template>


            </el-form-item>
        </el-form>

        <template v-if="value.optionsFromRemote">

            <el-form-item label="请求地址">
                <el-input @blur="blurEvent" size="small" v-model="value.remoteUrl" placeholder="获取选项地址" />
            </el-form-item>

        </template>
        <el-form-item label="选项展开">
            <el-switch v-model="value.expanding"></el-switch>
        </el-form-item>

        <el-form-item label="默认值">
            <el-select  :multiple ="fm.name==='MultipleSelect'" v-model="value.value" placeholder="请选择">
                <template v-if="!value.optionsFromRemote">
                    <el-option v-for="item in value.options" :key="item.key" :label="item.value" :value="item.key">
                    </el-option>
                </template>
                <template v-else>
                    <el-option v-for="item in value.remoteOptions" :key="item.key" :label="item.value" :value="item.key">
                    </el-option>

                </template>

            </el-select>
        </el-form-item>
    </div>
</template>

<script>
import draggable from "vuedraggable";
import { selectOptions } from "@/api/form";

export default {
    name: "SelectInputConfig",
    components: { draggable },
    props: {
        value: {
            type: Object,
            default: () => {
                return {}
            }
        },
        fm: {
            type: Object,
            default: () => {
                return {}
            }
        }
    },
    mounted(){
    console.log(this.fm)
  },
    data() {
        return {
            dragOption: {
                animation: 300,
                sort: true
            }
        }
    },
    methods: {
        blurEvent(e) {
            this.queryRemoteOption();
        },
        queryRemoteOption() {
            selectOptions({
                remoteUrl: this.value.remoteUrl
            }).then(res => {
                var data = res.data;
                this.value.remoteOptions = data;
            })
        },
    }
}
</script>

<style lang="less" scoped>
/deep/ .options {
    .el-form-item__label {
        display: block;
        width: 100%;
        text-align: left;
    }

    .el-icon-rank {
        padding-right: 5px;
        cursor: move;
    }

    .option-item {
        height: 80px;

        .el-input {
            width: 250px;
            float: right;
        }
    }
}

.option-item-label {
    height: 30px;
    line-height: 30px;

    div[class='self-switch'] {
        float: right;
    }
}

/*/deep/ .el-form-item {
  margin-bottom: 10px;

  .el-form-item__label {
    padding: 0;
  }
  .options{
    .el-icon-rank{
      cursor: move;
    }
  }
}*/
</style>
