<template>
  <span>
    <el-select size="small" placeholder="判断符" style="width: 120px;" :style="{marginLeft:compareLeftWidth+'px'}" v-model="condition.compare">
      <el-option :label="exp1.label" :value="exp1.value" :key="exp1.value"
        v-for="exp1 in expList"></el-option>
    </el-select>
    <el-date-picker v-model="condition.value[0]" value-format="yyyy-MM-dd" format="yyyy-MM-dd" type="date"
      style="margin-left: 10px;" :style="{width:compareValueWidth+'px'}" size="small" placeholder="选择日期">
    </el-date-picker>
  </span>
</template>

<script>
export default {

  name: 'ConditionShow',
  components: {},
  props: {

    condition: {
      type: Object,
      default: () => {
        return {}
      }
    },

      compareLeftWidth: {
          type: Number,
          default: 0
      },
      compareValueWidth: {
          type: Number,
          default: 280
      }
  },
  watch: {

  },
    mounted() {

    },
    data() {
    return {
      expList: [
                        { label: '等于', value: '==' },
                        { label: '大于', value: '>' },
                        { label: '大于等于', value: '>=' },
                        { label: '小于', value: '<' },
                        { label: '小于等于', value: '<=' }

                    ]
    }
  },
  methods: {}
}
</script>

