<template>
  <div>
    <el-form-item label="提示文字">
      <el-input size="small" v-model="value.placeholder" placeholder="请设置提示语"/>
    </el-form-item>
    <el-form-item label="是否多选">
      <el-switch v-model="value.multiple"></el-switch>
    </el-form-item>
    <el-form-item label="默认值">
      <el-button size="mini" icon="el-icon-plus" type="primary" @click="selectNoSetUser"  >选择</el-button>
          <org-items v-model="value.value"/>
      <org-picker title="选择默认值" :multiple="value.multiple" :type="pickerType()" ref="orgPicker"
      :selected="orgPickerSelected()"
                @ok="selected"/>
    </el-form-item>
  </div>

</template>

<script>
import OrgPicker from "@/components/common/OrgPicker";
import OrgItems from "@/views/common/process/OrgItems";
export default {
  name: "OrgPickerA",
  components: {OrgPicker,OrgItems},
  props:{
    value:{
      type: Object,
      default: ()=>{
        return {}
      }
    },
    fm:{
      type: Object,
      default: ()=>{
        return {}
      }
    }
  },
  mounted(){

  },
  data() {
    return {
      defaultValue:{

      }
    }
  },
  computed: {
    formId() {
      return this.fm.id
    },
  },
  watch:{

  },
  methods: {
    orgPickerSelected(){
      this.initData();
      return this.defaultValue[this.fm.id].orgPickerSelected
    },
    pickerType(){
      this.initData();
      return this.defaultValue[this.fm.id].selectOrgType
    },

    selectNoSetUser() {

      this.initData();

      this.defaultValue[this.fm.id].orgPickerSelected = this.value.value;

      this.$refs.orgPicker.show()
    },
    initData(){
      var k = this.defaultValue[this.fm.id];
     if(!k){
      this.$set(this.defaultValue,this.fm.id,{
        selectOrgType:this.fm.name==="DeptPicker"?'dept':'user',
        orgPickerSelected: []
      })
     }
    },
    selected(select) {

      this.initData();

     var k = this.defaultValue[this.fm.id];

      k.orgPickerSelected.splice(0,k.orgPickerSelected.length)
      select.forEach(val => k.orgPickerSelected.push(val))
      this.value.value=k.orgPickerSelected;
    },
  }
}
</script>

