<template>
    <div>
        <h4>选择嵌入式子流程发起人(<span class="desc">默认主流程发起人</span>)</h4>
        <el-radio-group v-model="selectStartUserType">
            <el-radio :label="1">同主流程发起人</el-radio>
            <el-radio v-if="formSingleUserList.length > 0" :label="2">选择表单中人员</el-radio>
            <!-- <el-radio :label="3">自定义发起人</el-radio> -->
        </el-radio-group>
        <div style="margin-top: 20px;">
            <template v-if="selectStartUserType === 3">
                <el-button size="mini" @click="selectOrg" icon="el-icon-plus" type="primary">请选择</el-button>
                <org-items v-model="selectStartUser" />
                <org-picker title="请选择子流程发起人" type="user" ref="orgPicker" :selected="selectStartUser" @ok="selected" />
            </template>
            <template v-if="selectStartUserType === 2">
                <el-select v-model="selectStartUserForm" placeholder="请选择">
                    <el-option v-for="item in formSingleUserList" :key="item.id" :label="item.title" :value="item.id">
                    </el-option>
                </el-select>
            </template>
        </div>

        <h4>是否需要子流程发起人审阅表单</h4>
      <el-switch
          v-model="selectStartUserCheckForm"
          active-text="需要"
          inactive-text="不需要">
      </el-switch>
        <h4>选择子流程</h4>
        <el-select v-model="selectChildProcessId" @change="childProcessChange" placeholder="请选择">
            <el-option v-for="item in processList" :key="item.formId" :label="item.formName" :value="item.formId">
            </el-option>
        </el-select>
        <h4>主子-表单映射(<span class="desc">主流程字段为必填项</span>）</h4>
        <el-button type="primary" @click="addOneLineInputFormMapping" size="mini">添加一行</el-button>
        <el-row style="margin-top: 20px;" v-for="(a, b) in inputSelectFormMapping" :key="a.id">
            <el-col :span="10">
                <el-select v-model="a.mainId" placeholder="主流程表单" >

                    <el-option v-for="item in mainRequiredFormSelect" :key="item.id" :label="item.title" :value="item.id">
                    </el-option>
                </el-select>
            </el-col>
            <el-col :span="10">
                <el-select v-if="a.mainId && a.mainId.length > 0" v-model="a.childId" placeholder="子流程表单">
                    <el-option v-for="item in getMatchChildProcessForm(a.mainId,a)" :key="item.id" :label="item.title"
                        :value="item.id">
                    </el-option>
                </el-select>
            </el-col>
            <el-col :span="2">
                <el-button @click="deleteInputFormMapping(b)" type="danger" icon="el-icon-delete"  ></el-button>
            </el-col>
        </el-row>
        <h4>子主-表单映射(<span class="desc">子流程字段为必填项</span>）</h4>
        <el-button type="primary" @click="addOneLineOutputFormMapping" size="mini">添加一行</el-button>
        <el-row style="margin-top: 20px;" v-for="(a, b) in outputSelectFormMapping" :key="a.id">
            <el-col :span="10">
                <el-select v-model="a.childId" placeholder="子流程表单"  >

                    <el-option v-for="item in childRequiredFormSelect" :key="item.id" :label="item.title" :value="item.id">
                    </el-option>
                </el-select>
            </el-col>
            <el-col :span="10">
                <el-select v-if="a.childId && a.childId.length > 0" v-model="a.mainId" placeholder="主流程表单">
                    <el-option v-for="item in getMatchMainProcessForm(a.childId,a)" :key="item.id" :label="item.title"
                        :value="item.id">
                    </el-option>
                </el-select>
            </el-col>
            <el-col :span="2">
                <el-button @click="deleteOutputFormMapping(b)" type="danger" icon="el-icon-delete"  ></el-button>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import OrgPicker from "@/components/common/OrgPicker";
import OrgItems from "../OrgItems";
import { ValueType } from "@/views/common/form/ComponentsConfigExport";
import { getFormGroups, getFormDetail } from "@/api/design"
export default {
    name: "RootConfig",
    components: { OrgPicker, OrgItems },
    props: {
        config: {
            type: Object,
            default: () => {
                return {}
            }
        }
    },
    mounted() {
        var t = this;
        this.getRemoteProcessGroup(function () {
            if (t.$isNotBlank(t.config.childProcessId)) {
                t.childProcessChange(t.config.childProcessId, false)
            }
        });

    },
    data() {
        return {
            showOrgSelect: false,
            processList: [],
            childProcessFormItems: []
        }
    },
    computed: {
        setup() {
            return this.$store.state.design
        },
        inputSelectFormMapping() {
            return this.config.inputFormMapping

        },
        outputSelectFormMapping() {
            return this.config.outputFormMapping

        },
        formItems() {
            return this.$store.state.design.formItems
        },
        mainRequiredFormSelect(){
            var f = this.formItems;

            return f.filter(res=>res.props.required===true);
        },

        childRequiredFormSelect(){
            var f = this.childProcessFormItems;

            return f.filter(res=>res.props.required===true);
        },
        formMap() {
            const map = new Map();
            this.formItems.forEach(item => this.itemToMap(map, item))
            return map
        },
        formSingleUserList() {
            //构造可用条件选项

            var ts = this.formItems.filter(item => item.valueType === ValueType.user
             &&
             item.props.multiple === false

             &&
             item.props.required === true
             );

            return ts
        },
        selectStartUser() {
            return this.config.startUser
        },
        selectStartUserType: {
            get() {
                return this.config.startUserType

            },
            set(val) {
                this.config.startUserType = val
            }
        },
        selectChildProcessId: {
            get() {
                return this.config.childProcessId

            },
            set(val) {
                this.config.childProcessId = val
            }
        },
        selectStartUserCheckForm: {
            get() {
                return this.config.startUserCheckForm

            },
            set(val) {
                this.config.startUserCheckForm = val
            }
        },

        selectChildProcessName: {
            get() {
                return this.config.childProcessName

            },
            set(val) {

                this.$set(this.config, 'childProcessName', val)
                //this.config.childProcessName = val
            }
        },
        selectStartUserForm: {
            get() {
                return this.config.startUserForm

            },
            set(val) {
                this.config.startUserForm = val
            }
        }
    },
    methods: {
        deleteInputFormMapping(i){
          this.inputSelectFormMapping.splice(i,1)
        },
        deleteOutputFormMapping(i){
          this.outputSelectFormMapping.splice(i,1)
        },

        getMatchChildProcessForm(f,item) {


            var a = this.formItems.filter(res => res.id === f)[0];

            var arr = this.childProcessFormItems.filter(res => res.valueType === a.valueType)


            return arr;
        },
        getMatchMainProcessForm(f,item) {

            var a = this.childProcessFormItems.filter(res => res.id === f)[0];
            if(!a){
                return [];
            }

            var arr = this.formItems.filter(res => res.valueType === a.valueType)


            return arr;
        },
        childProcessChange(v, clearMapping) {

            {
                var arr = this.processList.filter(res => res.formId === v);
                if (arr.length > 0) {

                    this.selectChildProcessName = arr[0].formName;

                }
            }

            if (clearMapping == undefined) {
                this.inputSelectFormMapping.splice(0, this.inputSelectFormMapping.length);
                this.outputSelectFormMapping.splice(0, this.outputSelectFormMapping.length);

            }

            getFormDetail(v).then(res => {

                var formItems = JSON.parse(res.data.formItems);
                this.childProcessFormItems = formItems
            })
        },
        addOneLineInputFormMapping() {
            this.inputSelectFormMapping.push({
                mainId: '',
                childId: ''
            })
        },
        addOneLineOutputFormMapping() {
            this.outputSelectFormMapping.push({
                mainId: '',
                childId: ''
            })
        },
        getRemoteProcessGroup(f) {
            getFormGroups({hidden:false}).then(res => {

                var data = res.data;
                for (var d of data) {
                    var items = d.items;
                    for (var it of items) {
                        var currentFormId = this.setup.formId;
                        if (this.$isNotBlank(currentFormId)) {
                            if (currentFormId === it.formId) {
                                continue;
                            }
                            this.processList.push(it)
                        } else {
                            this.processList.push(it)
                        }

                    }
                }
                if (f) {
                    f();
                }
            })
        },
        itemToMap(map, item) {
            map.set(item.id, item)
            if (item.name === 'SpanLayout') {
                item.props.items.forEach(sub => this.itemToMap(map, sub))
            }
        },
        selectOrg() {
            this.$refs.orgPicker.show()
        },
        selected(selectStartUser) {
            this.selectStartUser.length = 0
            selectStartUser.forEach(val => this.selectStartUser.push(val))
        },
        removeOrgItem(index) {
            this.selectStartUser.splice(index, 1)
        }
    }
}
</script>

<style lang="less" scoped>
.desc {
    font-size: small;
    color: #8c8c8c;
}

.org-item {
    margin: 5px;
}
</style>
