<template>
  <span>
    <el-select size="small" placeholder="判断符" style="width: 120px;" v-model="condition.compare"
               @change="compareChange"
               :style="{marginLeft:compareLeftWidth+'px'}">
                                    <el-option :label="exp.label" :value="exp.value" :key="exp.value"
                                               v-for="exp in explains"></el-option>
                                </el-select>
                                <span style="margin-left: 10px">
                                    <el-input :style="{width:compareValueWidth+'px'}"
                                              v-if="conditionValType(condition.compare) === 0"
                                              size="small" placeholder="输入比较值" type="number"
                                              v-model="condition.value[0]"/>
                                    <el-select :style="{width:compareValueWidth+'px'}" multiple filterable allow-create
                                               v-else-if="conditionValType(condition.compare) === 1" size="small"
                                               v-model="condition.value" placeholder="输入可能包含的值"></el-select>
                                    <!--                                    <span v-else-if="condition.compare&&condition.compare.length>0">-->
                                    <!--                                        <el-input style="width: 130px;" size="small" type="number"-->
                                    <!--                                                  placeholder="输入比较值"-->
                                    <!--                                                  v-model="condition.value[0]"/>-->
                                    <!--                                        <span> ~-->
                                    <!--                                            <el-input size="small" style="width: 130px;" type="number"-->
                                    <!--                                                      placeholder="输入比较值"-->
                                    <!--                                                      v-model="condition.value[1]"/>-->
                                    <!--                                        </span>-->
                                    <!--                                    </span>-->
                                </span>
                                </span>
</template>

<script>
export default {

    name: 'ConditionShow',
    components: {},
    props: {

        condition: {
            type: Object,
            default: () => {
                return {}
            }
        },
        compareLeftWidth: {
            type: Number,
            default: 0
        },
        compareValueWidth: {
            type: Number,
            default: 280
        }
    },
    mounted() {
        console.log(this.condition.compare)
    },
    watch: {},
    data() {
        return {
            explains: [
                {label: '等于', value: '=='},
                {label: '大于', value: '>'},
                {label: '大于等于', value: '>='},
                {label: '小于', value: '<'},
                {label: '小于等于', value: '<='},
                {label: '包含在', value: 'IN'},
            ]
        }
    },
    methods: {
        compareChange(v) {

        },

        conditionValType(type) {
            switch (type) {
                case '==':
                case '>':
                case '>=':
                case '<':
                case '<=':
                    return 0;
                case 'IN':
                    return 1;
                default:
                    return 2;
            }
        },
    }
}
</script>

<style scoped lang="less"></style>
