<template>
    <div>
        <el-radio-group v-model="nodeProps.nobody.handler">
            <el-radio label="TO_PASS">自动通过</el-radio>
            <el-radio label="TO_END">结束流程</el-radio>
            <el-radio label="TO_ADMIN">转交审批管理员</el-radio>
            <el-radio label="TO_USER">转交到指定人员</el-radio>
        </el-radio-group>

        <div style="margin-top: 10px" v-if="nodeProps.nobody.handler === 'TO_USER'">
            <el-button size="mini" icon="el-icon-plus" type="primary" @click="selectNoSetUser">选择人员</el-button>
            <org-items v-model="nodeProps.nobody.assignedUser" />
        </div>
        <org-picker title="选择指定人员1" type="user" ref="orgPicker" :selected="nodeProps.nobody.assignedUser" @ok="selected" />
    </div>
</template>

<script>
import OrgPicker from "@/components/common/OrgPicker";
import OrgItems from "../../OrgItems";

export default {
    name: "workSpace",
    components: { OrgPicker, OrgItems },
    props: {

    },
    data() {
        return {
        }
    },
    mounted() {

    },
    computed: {
        nodeProps() {
            return this.$store.state.selectedNode.props
        }
    },
    methods: {
        selectNoSetUser() {

            this.$refs.orgPicker.show()
        },
        selected(select) {

            select.forEach(val => this.nodeProps.nobody.assignedUser.push(val))
        }
    }
}
</script>

<style lang="less" scoped></style>
