<template>
  <span>
    <el-select size="small" placeholder="判断符" style="width: 120px;" v-model="condition.compare" :style="{marginLeft:compareLeftWidth+'px'}"
                                    @change="condition.value = []">
                                    <el-option label="等于" value="=="></el-option>
                                    <el-option label="包含在" value="IN"></el-option>
                                </el-select>
                                <span style="margin-left: 10px">
                                    <el-select v-if="condition.compare === 'IN'" :style="{width:compareValueWidth+'px'}"  clearable multiple
                                        size="small" v-model="condition.value" placeholder="选择值">
                                        <el-option v-for="(option, oi) in options" :key="oi"
                                            :label="option.value" :value="option.key"></el-option>
                                    </el-select>
                                    <el-select v-else  :style="{width:compareValueWidth+'px'}"  clearable size="small"
                                        v-model="condition.value[0]" placeholder="选择值">
                                        <el-option v-for="(option, oi) in options" :key="oi"
                                            :label="option.value" :value="option.key"></el-option>
                                    </el-select>
                                </span>
  </span>
</template>

<script>
export default {

  name: 'ConditionShow',
  components: {},
  props: {

    options: {
      type: Array,
      default: () => {
        return []
      }
    },
    condition: {
      type: Object,
      default: () => {
        return {}
      }
    },
      compareLeftWidth: {
          type: Number,
          default: 0
      },
      compareValueWidth: {
          type: Number,
          default: 280
      }
  },
  watch: {

  },
  data() {
    return {

    }
  },
  methods: {}
}
</script>

<style scoped lang="less"></style>
