<template>
    <div>

      <el-radio-group v-model="nodeProps.refuse.type">
        <el-radio label="TO_END">直接结束流程</el-radio>
        <!--          <el-radio label="TO_BEFORE">驳回到上级审批节点</el-radio>-->
        <el-radio label="TO_NODE">驳回到指定节点</el-radio>
      </el-radio-group>
      <div v-if="nodeProps.refuse.type === 'TO_NODE'">
        <span>指定节点:</span>
        <el-select style="margin-left: 10px; width: 150px;" placeholder="选择跳转步骤" size="small"
                   v-model="nodeProps.refuse.target">
          <el-option v-for="(node, i) in rejectNodeList" :key="i" :label="node.name" :value="node.id"></el-option>
        </el-select>
      </div>

           </div>
</template>

<script>

export default {
    name: "workSpace",
    components: {  },
    props: {
      fm: {
        type: Object,
        default: () => {
          return {}
        }
      }
    },
    data() {
        return {
        }
    },
    mounted() {

    },
    computed: {
        nodeProps() {
            return this.$store.state.selectedNode.props
        },

      rejectNodeList() {
        //驳回节点列表
        let values = []
        const excType = ['EMPTY', "CONDITION", "INCLUSIVES", "CONDITIONS", "CONCURRENT", "CONCURRENTS"]

        var arr = {

        }
        var obj = {}

        this.produceSerialNodeList(undefined, this.$store.state.design.process, arr, obj)


        var k = arr[this.fm.id];

        for (var item of k) {
          var type = obj[item].type;
          if (excType.indexOf(type) === -1&&obj[item].id!=this.fm.id) {
            values.push({ id: obj[item].id, name: obj[item].name })
          }
        }


        return values
      }
    },
    methods: {


      produceSerialNodeList(parentId, process, nodeArr, nodeObj) {

        if (!this.$isNode(process)) {
          return;
        }




        var nodeId = process.id;

        nodeObj[nodeId] = process;


        if (!this.$isNotBlank(parentId)) {
          var arr = []
          arr.push(nodeId)
          nodeArr[nodeId] = arr;
        } else {
          var p = nodeArr[parentId];
          var arr1 = this.$deepCopy(p);
          // if(arr.indexOf(parentId)==-1){
          //   arr.push(parentId)
          // }
          arr1.push(nodeId)
          nodeArr[nodeId] = arr1;
        }


        var type = process.type;

        var children = process.children;

        if (type === 'CONDITIONS' || type === 'CONCURRENTS' || type === 'INCLUSIVES') {
          children = children.children;

          var branchs = process.branchs;



          for (var item of branchs) {

            this.produceSerialNodeList(nodeId, item.children, nodeArr, nodeObj)

          }

          if (this.$isNode(children)) {

            this.produceSerialNodeList(nodeId, children, nodeArr, nodeObj)

          }

        } else {
          if (this.$isNode(children)) {
            this.produceSerialNodeList(nodeId, children, nodeArr, nodeObj)
          }
        }

      },
    }
}
</script>

<style lang="less" scoped></style>
