<template>
    <span>
        <el-select size="small" @change="selectPropChange" placeholder="判断符" style="width: 120px;"
            v-model="condition.userKey">
            <el-option :label="exp.label" :value="exp.key" :key="exp.key" v-for="exp in propList"></el-option>
        </el-select>
        <template v-if="getUserProp(condition.userKey).type === 'Self'">
            <el-button size="mini" icon="el-icon-plus" type="primary" style="margin-left: 10px;"
                @click="selectUser(condition.value, 'org')">选择人员/部门</el-button>
            <org-items v-model="condition.value" />
        </template>

        <template v-else-if="getUserProp(condition.userKey).type === 'Date'">

            <date-condition-show :key="uniKey" :compareValueWidth="135" :compareLeftWidth="10"
                :condition="condition"></date-condition-show>
        </template>


        <template v-else-if="getUserProp(condition.userKey).type === 'DateTime'">

            <date-time-condition-show :key="uniKey" :compareValueWidth="135" :compareLeftWidth="10"
                :condition="condition"></date-time-condition-show>
        </template>

        <template v-else-if="getUserProp(condition.userKey).type === 'Time'">

            <time-condition-show :key="uniKey" :compareValueWidth="135" :compareLeftWidth="10"
                :condition="condition"></time-condition-show>
        </template>


        <template v-else-if="getUserProp(condition.userKey).type === 'String'">

            <string-condition-show :key="uniKey" :compareValueWidth="135" :compareLeftWidth="10"
                :condition="condition"></string-condition-show>
        </template>
        <template v-else-if="getUserProp(condition.userKey).type === 'Number'">

            <number-condition-show :key="uniKey" :compareValueWidth="135" :compareLeftWidth="10"
                :condition="condition"></number-condition-show>
        </template>

        <template v-else-if="getUserProp(condition.userKey).type === 'Dept'">

<dept-condition-show :key="uniKey"   :compareLeftWidth="10"
    :condition="condition"></dept-condition-show>
</template>
        <template v-else-if="getUserProp(condition.userKey).type === 'Select'">

            <select-condition-show :key="uniKey" :compareValueWidth="135" :compareLeftWidth="10"
                :options="getUserProp(condition.userKey).options" :condition="condition"></select-condition-show>
        </template>

        <org-picker :type="orgType" multiple ref="orgPicker" :selected="users" @ok="selected"></org-picker>
    </span>
</template>

<script>
import DateConditionShow from "@/views/common/process/config/conditionshow/Date.vue";
import TimeConditionShow from "@/views/common/process/config/conditionshow/Time.vue";
import DateTimeConditionShow from "@/views/common/process/config/conditionshow/DateTime.vue";
import NumberConditionShow from "@/views/common/process/config/conditionshow/Number.vue";
import SelectConditionShow from "@/views/common/process/config/conditionshow/Select.vue";
import StringConditionShow from "@/views/common/process/config/conditionshow/String.vue";
import DeptConditionShow from "@/views/common/process/config/conditionshow/Dept.vue";
import OrgItems from "@/views/common/process/OrgItems.vue";
import OrgPicker from "@/components/common/OrgPicker.vue";
import { queryAllField } from '@/api/userField.js';

export default {

    name: 'ConditionShow',
    components: {
        OrgPicker,
        OrgItems,
        TimeConditionShow,
        StringConditionShow,
        DateTimeConditionShow,
        DateConditionShow,
        DeptConditionShow,
        NumberConditionShow,
        SelectConditionShow
    },
    props: {

        condition: {
            type: Object,
            default: () => {
                return {}
            }
        }
    },
    mounted() {
        this.users=this.condition.value;
    },
    watch: {},
    data() {
        return {

            uniKey: new Date().getTime(),
            users: [],
            orgType: 'org',
            propList: [
                {
                    label: '属于', key: 'id', type: 'Self', expList: [
                        {
                            label: '属于', value: '=='
                        }
                    ]
                },
                {
                    label: '部门', key: 'depId', type: 'Dept', expList: [

                    ]
                },
                {
                    label: '入职时间', key: 'entryDate', type: 'Date'
                },

                {
                    label: '性别', key: 'gender', type: 'Select', options: [
                        {
                            key: '1', value: '男'
                        },
                        {
                            key: '2', value: '女'
                        }
                    ]
                }

            ]
        }
    },
    methods: {

        selectPropChange(e) {

            this.uniKey = new Date().getTime();

            this.condition.value.splice(0, this.condition.value.length);
            this.condition.compare = '';
            this.users.splice(0, this.users.length);

            this.condition.userType = this.getUserProp(e).type;
        },
        selected(select) {
            this.users.length = 0
            select.forEach(u => this.users.push(u))
        },
        selectUser(value, orgType) {
            this.orgType = orgType
            this.users = value
            this.$refs.orgPicker.show()
        },
        getUserProp(e) {
            var k = this.propList.filter(res => res.key === e)[0];
            if (!k) {
                return {
                    expList: []
                }
            }
            return k;
        },
    }
}
</script>

<style scoped lang="less"></style>
