<template>
    <div class="senior-setup">


    </div>
</template>

<script>
var checkURL = (rule, value, callback) => {
    if (!value) {
        return callback(new Error('URL不能为空'));
    }

    if (!(/^[a-zA-z]+:\/\/[^\s]*/.test(value))) {
        return callback(new Error('请输入URL'));
    }
    if (value.length > 200) {
        return callback(new Error('URL不能超过200个字符'));

    }
    callback();

};


export default {
    name: "FormProSetting",
    computed: {
        setup() {
            return this.$store.state.design.settings;
        },
        formItems() {
            var formItems = this.$store.state.design.formItems;

            var t = JSON.parse(JSON.stringify(formItems))

            t.unshift({ id: 'root', title: '发起人', valueType: 'User' })
            return t
        },
    },
    data() {
        return {
            rules: {
                url: [
                    { validator: checkURL, trigger: 'blur' }
                ]
            }
        }
    }, mounted() {
        console.log(this.setup)
    },
    methods: {
        delFormItem( s,index) {
            console.log(index)
             this.setup[s].params.splice(index, 1);
        },
        addEndEventForm() {
            this.setup.endEventForm.params.push({
                name: '', value: '', isField: true
            })
        },
        addStartEventForm() {
            this.setup.startEventForm.params.push({
                name: '', value: '', isField: true
            })
        },
        validate() {
            let err = []
            //先检查开始事件
            var startEventForm = this.setup.startEventForm;
            var endEventForm = this.setup.endEventForm;

            if (startEventForm.status) {
                //开启
                if (!startEventForm.url) {
                    err.push('请填写开始事件URL')
                } else if (!(/^[a-zA-z]+:\/\/[^\s]*/.test(startEventForm.url))) {
                    err.push('请填写开始事件URL')
                } else if (startEventForm.url.length > 200) {
                    err.push("开始事件URL不能超过200个字符")
                }

                var params = startEventForm.params;

                var keyList = params.map(res => res.name);

                var newArr = [...new Set(keyList)]; //利用了Set结构不能接收重复数据的特点
                if (newArr.length < keyList.length) {
                    err.push("开始事件的自定义字段不能重复")

                }

                for (var item of params) {
                    var key = item.name;
                    var field = item.value;
                    var isField=item.isField;
                    if (!key || key.length < 1 || key.length > 20) {
                        err.push('请填写开始事件表单自定义参数')
                    }
                    if (!field || field.length < 1) {
                        err.push('请选择开始事件表单')
                    }

                    if(isField){
                        if (!field || field.length < 1) {
                        err.push('请选择开始事件表单')
                    }
                    }else{
                        if (!field || field.length < 1||field.length>20) {
                        err.push('请填写开始事件参数值')
                    }
                    }
                }

            }


            if (endEventForm.status) {
                //开启
                if (!endEventForm.url) {
                    err.push('请填写结束事件URL')
                } else if (!(/^[a-zA-z]+:\/\/[^\s]*/.test(endEventForm.url))) {
                    err.push('请填写结束事件URL')
                } else if (endEventForm.url.length > 200) {
                    err.push("结束事件URL不能超过200个字符")
                }

                var params = endEventForm.params;

                var keyList = params.map(res => res.name);

                var newArr = [...new Set(keyList)]; //利用了Set结构不能接收重复数据的特点
                if (newArr.length < keyList.length) {
                    err.push("结束事件的自定义字段不能重复")

                }

                for (var item of params) {
                    var key = item.name;
                    var field = item.value;
                   var isField= item.isField;
                    if (!key || key.length < 1 || key.length > 20) {
                        err.push('请填写结束事件表单自定义参数')
                    }
                    if(isField){
                        if (!field || field.length < 1) {
                        err.push('请选择结束事件表单')
                    }
                    }else{
                        if (!field || field.length < 1||field.length>20) {
                        err.push('请填写结束事件参数值')
                    }
                    }

                }

            }
            if (err.length > 0) {
                this.$refs['startEventForm'].validate((valid) => {

                });

                this.$refs['endEventForm'].validate((valid) => {

                });
            } else {
                // this.setup.startEventForm=startEventForm;
                // this.setup.endEventForm=endEventForm;
            }

            return err
        }
    }
}
</script>

<style lang="less" scoped>
.senior-setup {
    overflow: auto;
    margin: 0 auto;
    width: 600px;
    min-height: calc(100vh - 105px);
    background: #ffffff;
    margin-top: 10px;
    padding: 15px 20px;

    .sign-tip {
        color: #949495;
        font-size: small;
        margin-left: 20px;
    }
}

@variable_btn: 140px;
@variable_del_btn: 60px;

.f1 {
    float: left;
    width: calc(50% - @variable_btn/2 - 10px - @variable_del_btn/2);
    height: 40px;
    margin: 0 5px;
    display: inline-block;
}

.f2 {
    float: left;
    width: @variable_btn;
    margin: 0 5px;
    height: 40px;
}

.f3 {
    float: left;
    width: calc(50% - @variable_btn/2 - 10px - @variable_del_btn);
    margin: 0 5px;
    height: 40px;
}

.f4 {
    float: left;
    width: @variable_del_btn;
    margin: 0 5px;
    height: 40px;
    line-height: 40px;
}</style>
