<template>
  <span>
    <el-select size="small" placeholder="判断符" style="width: 120px;" v-model="condition.compare" :style="{marginLeft:compareLeftWidth+'px'}"
                                    @change="condition.value = []">
                                    <el-option label="等于" value="=="></el-option>
                                    <el-option label="包含在" value="IN"></el-option>
                                </el-select>

                                <span  style="margin-left: 10px">
                                    <el-input v-if="condition.compare === '=='"  :style="{width:compareValueWidth+'px'}"  placeholder="输入比较值"
                                        size="small" v-model="condition.value[0]" />
                                    <el-select  v-if="condition.compare === 'IN'" :style="{width:compareValueWidth+'px'}"  multiple clearable filterable allow-create
                                        size="small" v-model="condition.value" placeholder="输入可能包含的值"></el-select>
                                </span>
  </span>
</template>

<script>
export default {

  name: 'ConditionShow',
  components: {},
  props: {

    condition: {
      type: Object,
      default: () => {
        return {}
      }
    }, compareLeftWidth: {
            type: Number,
            default: 0
        },
        compareValueWidth: {
            type: Number,
            default: 280
        }
  },
  watch: {

  },
  data() {
    return {

    }
  },
  methods: {}
}
</script>

<style scoped lang="less"></style>
