<template>
    <div>
        <div v-for="(group, index) in selectedNode.props.groups" :key="index + '_g'" class="group">
            <div class="group-header">
                <span class="group-name">条件组 {{ groupNames[index] }}</span>
                <div class="group-cp">
                    <span>组内条件关系：</span>
                    <el-switch v-model="group.groupType" active-color="#409EFF" inactive-color="#c1c1c1" active-value="AND"
                        inactive-value="OR" active-text="且" inactive-text="或" />
                </div>
                <div class="group-operation">

                    <el-dropdown @command="addConditionHandler" @visible-change="conditionVisibleChange">
                        <span class="el-dropdown-link">
                            <i class="el-icon-plus"></i>

                        </span>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item selftest :command="cindex + '-' + index"
                                v-for="(condition, cindex) in conditionList" :key="condition.id + '_' + cindex">{{
                                    condition.title }}
                            </el-dropdown-item>

                        </el-dropdown-menu>
                    </el-dropdown>
                    <i class="el-icon-delete" @click="delGroup(index)"></i>
                </div>
            </div>
            <div class="group-content">
                <p v-if="group.conditions.length === 0">点击右上角 + 为本条件组添加条件 ☝</p>
                <div v-else>
                    <el-form ref="condition-form" label-width="100px">
                        <!--构建表达式-->
                        <el-form-item v-for="(condition, cindex) in group.conditions" :key="condition.id + '_' + cindex">
                            <ellipsis slot="label" hover-tip :content="condition.title" />
                            <span v-if="condition.valueType === ValueType.string">
                                <string-condition-show v-if="!isSelect(condition.id)"
                                    :condition="condition"></string-condition-show>
                                <select-condition-show v-else :condition="condition"
                                    :options="getOptions(condition.id)"></select-condition-show>

                            </span>
                            <span v-else-if="condition.valueType === ValueType.number">
                                <number-condition-show :condition="condition"></number-condition-show>
                            </span>
                            <span v-else-if="condition.valueType === ValueType.user">

                                <user-condition-show :condition="condition"></user-condition-show>

                            </span>
                            <span v-else-if="condition.valueType === ValueType.dept">
                                <!-- <span class="item-desc" style="margin-right: 20px">为某部门 / 某部门下的部门</span>
                                <el-button size="mini" icon="el-icon-plus" type="primary"
                                    @click="selectUser(condition.value, 'dept')">选择部门</el-button>
                                <org-items v-model="condition.value" /> -->
                                <dept-condition-show :condition="condition"></dept-condition-show>

                            </span>
                            <span v-else-if="condition.valueType === ValueType.date">

                                <template v-if="condition.dateType === 'date'">
                                    <date-condition-show :condition="condition"></date-condition-show>
                                </template>
                                <template v-if="condition.dateType === 'datetime'">
                                    <date-time-condition-show :condition="condition"></date-time-condition-show>
                                </template>
                                <template v-if="condition.dateType === 'time'">
                                    <time-condition-show :condition="condition"></time-condition-show>
                                </template>


                            </span>
                            <i class="el-icon-delete" @click="rmSubCondition(group, cindex)"></i>
                        </el-form-item>
                    </el-form>
                </div>
            </div>
        </div>
        <org-picker :type="orgType" multiple ref="orgPicker" :selected="users" @ok="selected"></org-picker>
    </div>
</template>

<script>
import OrgPicker from "@/components/common/OrgPicker";
import OrgItems from '../OrgItems'
import { ValueType } from '@/views/common/form/ComponentsConfigExport'
import DateConditionShow from './conditionshow/Date.vue'
import StringConditionShow from './conditionshow/String.vue'
import SelectConditionShow from './conditionshow/Select.vue'
import NumberConditionShow from './conditionshow/Number.vue'
import DateTimeConditionShow from './conditionshow/DateTime.vue'
import TimeConditionShow from './conditionshow/Time.vue'
import UserConditionShow from './conditionshow/User.vue'
import DeptConditionShow from './conditionshow/Dept.vue'
export default {
    name: "ConditionGroupItemConfig",
    components: { OrgPicker, OrgItems,DeptConditionShow, TimeConditionShow, DateTimeConditionShow, UserConditionShow,DateConditionShow, StringConditionShow, SelectConditionShow, NumberConditionShow },
    data() {
        return {
            ValueType,
            users: [],
            orgType: 'user',
            showOrgSelect: false,
            //groupConditions: [],
            groupNames: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J'],
            supportTypes: [ValueType.number, ValueType.string, ValueType.date, ValueType.dept, ValueType.user],
            userExplains: [
                {
                    label: '属于', key: 'id', expList: [
                        {
                            label: '属于', value: '=='
                        }
                    ]
                },
                {
                    label: '年龄', key: 'age', expList: [
                        { label: '等于', value: '=' },
                        { label: '大于', value: '>' },
                        { label: '大于等于', value: '>=' },
                        { label: '小于', value: '<' },
                        { label: '小于等于', value: '<=' },
                        { label: '包含在', value: 'IN' }

                    ]
                },

                {
                    label: '入职时间', key: 'entryDate', expList: [
                        { label: '等于', value: '=' },
                        { label: '大于', value: '>' },
                        { label: '大于等于', value: '>=' },
                        { label: '小于', value: '<' },
                        { label: '小于等于', value: '<=' },
                        { label: '包含在', value: 'IN' }

                    ]
                },
                {
                    label: '性别', key: 'gender', expList: [
                        { label: '等于', value: '=' }

                    ]
                },
            ],
            explains: [
                { label: '等于', value: '=' },
                { label: '大于', value: '>' },
                { label: '大于等于', value: '>=' },
                { label: '小于', value: '<' },
                { label: '小于等于', value: '<=' },
                { label: '包含在', value: 'IN' },

            ]
        }
    },

    computed: {
        selectedNode() {
            return this.$store.state.selectedNode
        },
        select() {
            return this.selectedNode.props.assignedUser || []
        },
        formItems() {
            return this.$store.state.design.formItems
        },
        formMap() {
            const map = new Map();
            this.formItems.forEach(item => this.itemToMap(map, item))
            return map
        },
        conditionList() {
            //构造可用条件选项
            const conditionItems = []
            this.formItems.forEach(item => this.filterCondition(item, conditionItems))
            if (conditionItems.length === 0 || conditionItems[0].id !== 'root') {
                conditionItems.unshift({ id: 'root', title: '发起人', valueType: 'User' })
            }
            return conditionItems
        }
    },
    mounted() {

    },
    methods: {
        getUserExplains(e) {
            var k = this.userExplains.filter(res => res.key === e)[0];
            if (!k) {
                return {
                    expList: []
                }
            }
            return k;
        },
        itemToMap(map, item) {
            map.set(item.id, item)
            if (item.name === 'SpanLayout') {
                item.props.items.forEach(sub => this.itemToMap(map, sub))
            }
        },
        isSelect(formId) {
            let form = this.formMap.get(formId)
            if (form && (form.name === 'SelectInput' || form.name === 'MultipleSelect')) {
                return true
            }
            return false
        },
        conditionValType(type) {
            switch (type) {
                case '=':
                case '>':
                case '>=':
                case '<':
                case '<=':
                    return 0;
                case 'IN':
                    return 1;
                default:
                    return 2;
            }
        },
        selectUser(value, orgType) {
            this.orgType = orgType
            this.users = value
            this.$refs.orgPicker.show()
        },
        filterCondition(item, list) {
            if (item.name === 'SpanLayout') {
                item.props.items.forEach(sub => this.filterCondition(sub, list))
                //包括非必填的
                // }else if (this.supportTypes.indexOf(item.valueType) > -1 && item.props.required){
            } else if (this.supportTypes.indexOf(item.valueType) > -1) {
            
                //人员多选排除
                if(item.name==="UserPicker"&&item.props.multiple){
                    return;
                }
                list.push({
                    title: item.title,
                    id: item.id,
                    valueType: item.valueType,
                    format: item.valueType === 'Date' ? item.props.format : '',
                    dateType: item.valueType === 'Date' ? item.dateType : ''
                })
            }
        },
        getOptions(formId) {
            var props = this.formMap.get(formId).props;
            if (props.optionsFromRemote && props.remoteUrl.length > 0) {
                return props.remoteOptions || []
            }
            return props.options || []
        },
        selected(select) {
            this.users.length = 0
            select.forEach(u => this.users.push(u))
        },
        delGroup(index) {
            this.selectedNode.props.groups.splice(index, 1)
        },
        rmSubCondition(group, index) {
            group.conditions.splice(index, 1)
        },
        conditionVisibleChange(e) {
            console.log(e)
        },
        addConditionHandler(cmd) {

            let condition = { ...this.conditionList[parseInt(cmd.split('-')[0])] }
            condition.compare = '';
            condition.value = []
            this.selectedNode.props.groups[parseInt(cmd.split('-')[1])].conditions.push(condition)
        }

    }
}
</script>

<style lang="less" scoped>
.group {
    margin-bottom: 20px;
    color: #5e5e5e;
    overflow: hidden;
    border-radius: 6px;
    border: 1px solid #e3e3e3;

    .group-header {
        padding: 5px 10px;
        background: #e3e3e3;
        position: relative;

        div {
            display: inline-block;
        }

        .group-name {
            font-size: small;
        }

        .group-cp {
            font-size: small;
            position: absolute;
            left: 100px;
            display: flex;
            top: 5px;
            justify-content: center;
            align-items: center;
        }

        .group-operation {
            position: absolute;
            right: 10px;

            i {
                padding: 0 10px;

                &:hover {
                    cursor: pointer;
                }
            }
        }
    }

    .group-content {
        padding: 10px 5px;

        p {
            text-align: center;
            font-size: small;
        }

        .el-icon-delete {
            position: absolute;
            cursor: pointer;
            top: 12px;
            right: 0;
        }
    }

    .condition-title {
        display: block;
        width: 100px;
    }
}
</style>
